@import '../../../../common/styles/mixins';

.header {
    margin-bottom: 20px;

    .headerContainer {
        display: flex;
        justify-content: space-between;
        margin: 40px 0 0;

        h2 {
            @include title();
            text-align: start;

            .btnMenu {
                background-color: inherit;
                border: none;
                position: relative;
            }

            .icon {
                border-radius: 50%;
                border: 1px solid;
                width: 15px;
                height: 15px;
                padding: 5px;
                cursor: pointer;
                margin-left: 10px;
            }
        }
    }
}
